<template>
  <div class="favorite-list">
    <ul>
      <li v-for="(item,index) in favoritelists" :key="index">
        <!-- 左 -->
        <p class="favorite-left">
          <img :src="item.imgUrl" alt class="favorite-img">
        </p>
        <!-- 中 -->
        <p class="favorite-center">
          <span class="title">{{item.title}}</span>
          <span class="desc">{{item.desc}}</span>
        </p>
        <!-- 右 -->
        <p class="favorite-right">
          <img
            v-if="item.favoritelists!=='宝宝最爱'"
            class="favorite-symbol"
            :src="item.symbol"
            alt
            @click="showPop(index)"
          >
        </p>
      </li>
    </ul>
    <img class="favorit-add" src="static/images/favorite/add.png" alt @click="addPop()">
    <mt-popup v-model="popupVisible" position="bottom" :closeOnClickModal=false>
      <div class="tanchuang">
        <div class="x-edit">
          <span class="res-name" @click="resname()">重命名</span>
          <span class="del" @click="del()">删除</span>
        </div>
        <span class="x-cancel" @click="cancel()">取消</span>
      </div>
    </mt-popup>

    <!-- 编辑名称 -->
    <mt-popup v-model="addPopupVisible" position="center" >
      <div class="custom-tanchuang">
        <p class="custom-name" v-if="isRname">重命名</p>
        <p class="custom-name" v-else>请输入自定义专辑名称</p>
        <div class="text-center">
          <input v-model="titleName" class="text-input" type="text" placeholder="请输入自定义专辑">
        </div>
        <div class="from-bottom">
          <span class="custom-cancel" @click="customcancel()">取消</span>
          <span @click="customconfirm(isRname)" class="custom-sure">确认</span>
        </div>
      </div>
    </mt-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      popupVisible: false,
      addPopupVisible: false,
      titleName: "",
      currentIndex: 0,
      isRname: false,
      favoritelists: [
        {
          imgUrl: "static/images/favorite/0.png",
          title: "宝宝最爱",
          desc: "0首"
        },
        {
          imgUrl: "static/images/favorite/1.png",
          title: "宝宝最爱",
          desc: "1首",
          symbol: "static/images/favorite/edit.png"
        },
        {
          imgUrl: "static/images/favorite/2.png",
          title: "宝宝最爱",
          desc: "120首",
          symbol: "static/images/favorite/edit.png"
        }
      ]
    };
  },
  methods: {
    showPop(index) {
      this.popupVisible = true;
      this.currentIndex = index;
    },
    addPop() {
      this.addPopupVisible = true;
    },
    resname() {
      this.addPopupVisible = true;
      this.popupVisible = false;
      this.isRname = true;
      this.titleName = this.favoritelists[this.currentIndex].title;
    },
    del() {
      let that = this;
      that.favoritelists.splice(that.currentIndex, 1);
      that.cancel();
      that.$toast("删除成功!");
      // this.favoritelists.forEach(function(element, index) {
      //   if (index === that.currentIndex) {
      //     that.favoritelists.splice(index, 1);
      //     that.cancel();
      //     that.$toast("删除成功!");
      //   }
      // });
    },
    cancel() {
      this.popupVisible = false;
    },
    customcancel() {
      this.popupVisible = false;
      this.addPopupVisible = false;
      this.isRname = false;
      this.titleName = "";
    },
    customconfirm(isRname) {
      if (isRname) {
        this.favoritelists[this.currentIndex].title = this.titleName;
        this.$toast("重命名成功");
        this.isRname = false;
      } else {
        if (this.titleName.length === 0) {
          this.$toast("收藏夹名字不能为空");
          return;
        }
        let imgArr = [
          "static/images/favorite/1.png",
          "static/images/favorite/2.png"
        ];
        let index = Math.floor(Math.random() * imgArr.length);
        let item = {
          imgUrl: imgArr[index],
          title: this.titleName,
          desc: "0首",
          symbol: "static/images/favorite/edit.png"
        };
        item.title = this.titleName;
        this.favoritelists.push(item);
        this.$toast("添加成功");
      }
      this.titleName = "";
      this.addPopupVisible = false;
    }
  }
};
</script>

<style lang="scss" scoped>
ul li {
  display: flex;
  border-bottom: solid 0.05rem #f7f7f7;
}
.favorite-left {
  margin: 0.813rem 0 0 1.5rem;
}
.favorite-img {
  width: 3.125rem;
  margin-bottom: 0.8rem;
}
.favorite-center {
  width: 100%;
}
.title {
  display: block;
  font-size: 0.938rem;
  letter-spacing: 0.072rem;
  color: #1c1b1b;
  margin: 0.906rem 0 0 0.875rem;
}
.desc {
  font-size: 0.688rem;
  letter-spacing: 0.053rem;
  color: #898989;
  margin: 0.25rem 0 0 0.875rem;
}
.favorite-symbol {
  width: 1.25rem;
  margin: 1.625rem 1.563rem 0 0;
}
.favorit-add {
  height: 3.25rem;
  margin: 2rem auto 0 auto;
}

//重命名删除弹窗
.tanchuang {
  width: 20.438rem;
  text-align: center;
}
.x-edit {
  height: 5rem;
  border: 0px solid #fff;
  border-radius: 8px;
  margin-bottom: 0.8rem;
  background: rgba(229, 229, 229, 0.8);
}
span {
  display: block;
}
.res-name {
  font-stretch: normal;
  color: #030303;
  height: 2.5rem;
  line-height: 2.5rem;
  border-bottom: 1px solid #d5d5d5;
}
.del {
  color: #030303;
  height: 2.5rem;
  line-height: 2.5rem;
}
.x-cancel {
  color: #030303;
  height: 2.5rem;
  line-height: 2.5rem;
  border: 0px solid #fff;
  border-radius: 8px;
  background: rgba(229, 229, 229, 0.8);
}

//增加收藏弹窗
.custom-tanchuang {
  width: 20.438rem;
  text-align: center;
}
.custom-name {
  font-stretch: normal;
  height: 2.5rem;
  line-height: 2.5rem;
  background-color: #ebebeb;
  border: 0px solid #d5d5d5;
}
.text-center {
  height: 3rem;
  line-height: 3rem;
  background-color: #fff;
}
.text-input {
  width: 70%;
  height: 2rem;
  font-size: 12px;
  border: 1px solid #f1f1f1;
  border-radius: 5px;
}
.from-bottom {
  display: flex;
  height: 2.5rem;
  line-height: 2.5rem;
  background-color: #ebebeb;
  text-align: center;
}
.custom-cancel {
  width: 50%;
  border-right: 1px solid #fff;
}
.custom-sure {
  width: 50%;
  border-left: 1px solid #fff;
}
</style>



